/** Media Queries **/
.access {
  padding: 3rem;
  background-color: rgb(227.124137931, 226.475862069, 226.475862069);
  height: 100vh;
}
.access h3 {
  text-align: center;
  margin-bottom: 6rem;
  color: #383D3B;
  text-transform: uppercase;
}
.access label {
  color: #383D3B;
}
.access .login-btn {
  background-color: transparent;
  padding: 1.5rem 3rem;
  color: #e6e6dd;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 400;
  border: solid 0.2rem #e6e6dd;
  border-radius: 0.5rem;
  transition: all 0.3s ease;
  text-align: center;
  display: block;
  width: 100%;
  letter-spacing: 0.1rem;
  box-shadow: 0rem 0.6rem 1.5rem -0.6rem #383D3B;
  margin: 6rem auto;
}
@media (min-width: 768px) {
  .access .login-btn {
    width: 30rem;
    font-size: 2rem;
  }
}
.access .login-btn:hover {
  transform: scale(1.05);
  color: #AB9144;
  border: solid 0 #AB9144;
  box-shadow: 0 0 0.5rem 0.3rem #AB9144;
  cursor: pointer;
}
.access .descripcion-pagina {
  margin-top: 10rem;
  text-align: center;
  font-size: 2.4rem;
}
.access .enlace-login {
  display: flex;
  color: #709317;
  justify-content: center;
  font-size: 1.7rem;
}

