@use 'base/variables' as v;
@use 'base/mixins' as m;

.form {
    .field {
        display: flex;
        align-items: center;
        margin-bottom: 2rem;
        label {
            flex: 0 0 10rem;
            font-size: 1.4rem;
            color: v.$lightGrey;
            @include m.tablet {
                flex: 0 0 12rem;
                font-size: 1.7rem;
            }
            &.wider{
                flex: 0 0 8rem; 
            }
        }
        input, select, textarea {
            flex: 0;
            min-width: 70%; // esto hace que en celulares los inputs no tomen mas tamao del asignado
            padding: 1rem;
            border: none;
            background-color: v.$white;
            border: solid 0.2rem adjust-color(v.$grey, $lightness: 46%);
            border-radius: .5rem;
            font-size: 1.4rem;
            color: v.$grey;
            @include m.tablet {
                flex: 1;
                font-size: 1.7rem;
            };
            &.custom-file-input {
                padding: .4rem;
              }
        }
        textarea {
            height: 12rem;
        }
        select{
            cursor: pointer;
            appearance: none;
            background-color: v.$white;
            color: v.$grey;
            &::-ms-expand{
                display: none;
            }
        }
    }
    .custom-file-input::-webkit-file-upload-button {
        visibility: hidden;
      }
      .custom-file-input::before {
        content: 'Elegir fotos';
        display: inline-block;
        background-color: adjust-color(v.$greenBrand, $lightness: 10%);
        border: 0.2rem solid adjust-color(v.$white, $lightness: 0%);
        border-radius: .5rem;
        padding: .65rem;
        outline: none;
        white-space: nowrap;
        font-size: 1.2rem;
        letter-spacing: 1.2px;
        cursor: pointer;
        font-weight: v.$bold;
        text-transform: uppercase;
        color: v.$white;
      }
      .custom-file-input::after {
        display: none;
      }
      .custom-file-input:hover::before {
        background-color: v.$darkGrey;
        border-color: adjust-color(v.$darkGrey, $lightness: 7%);
        color: v.$white;
      }
    option {
        cursor: pointer;
        &:hover{
            background-color: adjust-color(v.$white, $lightness: 15%);
        }
    }
    select option:checked{
            background-color: adjust-color(v.$orangeFlame, $lightness: 20%);
    }
    .button{
        @include m.button(transparent, v.$white, v.$white);
        margin-top: 6rem;
        @include m.tablet {
            margin: 6rem auto;
        }
    }
}