@use 'base/variables' as v;
@use 'base/mixins' as m;

.footerbar{
    box-shadow: 0 -.1rem 2rem -.5rem v.$white;
    padding: 3rem;
    margin: 0;
    @include m.tablet {
        height: 100%;
    }
    h3,li, a, p{
        color: v.$white;
    }
    a, p, li{
        font-size: 1.6rem;
    }
    h3{
        text-align: center;
        margin: 0 0 3rem;
        text-transform: uppercase;
    }

    .footer-container{
        @include m.tablet {
            display: flex;
            justify-content: space-between;
        }

        .footer-links{
            h3{
                text-align: center;
            }
            
            li{
                margin: .1rem 0;
                text-align: center;   
            }
            @include m.tablet {
                flex: 0 0 30%;
                h3{
                    text-align: start;
                    height: 7rem;
                }
                li{
                    text-align: start;   
                }
            }
        }
        .footer-contact{
            margin-bottom: 2rem;
            @include m.tablet {
                flex: 0 0 40%;
                margin-bottom: 0;
            }
            h3{
                @include m.tablet {
                    height: 7rem;
                }
            }
            .info_contact{
                display: flex;
                flex-direction: column;
                align-items: center;
            }
            p{
                align-content: center;
                margin: 0.2rem 0;
                max-width: 20rem;

                text-align: center;
                line-height: 1.2;
                
            }
        }
        .footer-socialMedia{
            text-align: center;
            @include m.tablet {
                flex: 0 0 30%;
            }
            h3{
                @include m.tablet {
                    height: 7rem;
                    text-align: end;
                }
            }
            li{
                margin: .1rem 0;
                @include m.tablet {
                    text-align: end;
                }
            }
        }

        .footer-links, .footer-contact, .footer-socialMedia{
            padding: 2rem;
        }

        .links-list{
            list-style: none;
            padding-left: 0;
        }
    }

    .politics{
        p{margin: 0;}
        a{align-content: center;}
        margin: 5rem 0 2rem;            
        
        .laws{
            display: flex;
            justify-content: center;
            gap: 1rem;
            margin: 1rem 0;
            @include m.tablet {
                margin: 0;
            }
            p{
                align-content: center;
            }
        }
        .copyright{
            text-align: center;
        }
    }

}