@use 'base/variables' as v;
@use 'base/mixins' as m;

.botsAI{
    .hero{
        background-image: linear-gradient(rgb(0, 0, 0), rgba(32, 32, 32, 0.7)),
        url(../../build/gif/botAI.gif);
    }
}
.contact{
    .hero{
        background-image: linear-gradient(rgb(0, 0, 0), rgba(32, 32, 32, 0.5)),
        url(../../build/gif/contact.gif);
    }
}
.hero{
    background-size: cover;
    background-position: center center;
    height: 100vh;
    max-width: 100%;
    align-content: end;
    
    .hero_content{
        h1 {
            text-align: start;
            padding: 0 2rem;
            font-size: 12rem;
            @include m.tablet {
                font-size: 22rem; 
            }
            color: v.$white;
            font-weight: v.$bold;
            max-width: 100%;
            text-transform: uppercase;
            display: flex;
            gap: 1rem;
        }
    }
    .hero_content h1 {
        white-space: nowrap; // Evita que las palabras se dividan en mltiples lneas
        overflow: hidden; // Oculta el desbordamiento de las palabras
        span {
            gap: 1rem;
            display: inline-block;
            animation: moveLeft 60s linear infinite; // Aplica la animacin a cada palabra
        }
    }
    @keyframes moveLeft {
        0% { transform: translateX(4%); } // Inicia desde la derecha
        100% { transform: translateX(-100%); } // Se desplaza hacia la izquierda
        
        @include m.tablet {
            0% { transform: translateX(10%); } // Inicia desde la derecha
            100% { transform: translateX(-100%); } // Se desplaza hacia la izquierda
        }
    }
}

.section_content{
    padding: 1rem;
    margin: 10rem 0;
    .title{
        margin: 40rem 0;
        padding: 0;
        font-size: 4rem;
        @include m.tablet {
            font-size: 7rem;
        }
        @include m.desktop {
            font-size: 14rem;
        }
        text-transform: uppercase;
        color: v.$white;
    }
    .section_div{
        padding: 1rem;
        margin: 40rem 0;
        @include m.desktop {
            padding: 0 5rem;
        }
        &:last-of-type{
            margin: 0;
        }
        .split{
            justify-content: space-between;
            @include m.tablet {
                display: flex;
                gap: 4rem;
                padding: 1rem;
            }
            @include m.desktop {
                gap: 8rem;
            }
            .section_title{
                text-align: center;
                text-transform: uppercase;
                color: v.$white;
                margin-bottom: 4rem;
                font-size: 3rem;
                @include m.tablet {
                    font-size: 4rem;
                    flex: 0 0 40%;
                    margin: 0;
                    align-content: start;
                    word-break: break-word;
                }
                @include m.desktop {
                    font-size: 6rem;
                }
                span{
                    padding: 0 1rem;
                    @include m.tablet {
                        padding: 0 2rem;
                        
                    }
                }
            }
            &.mbig{
                @include m.tablet {
                    margin-bottom: 30rem;
                }
            }
            .section_text{    
                @include m.tablet {
                    width: 100%;
                    align-content: start;
                }
                details{
                    summary{
                        color: v.$white;
                        font-family: v.$font_variant;
                        font-size: 2rem;
                        @include m.tablet {
                            font-size: 4rem;
                            margin-bottom: 3rem;

                        }
                        &:hover{
                            cursor: pointer;
                        }

                    }
                    p{
                        color: v.$white;
                        font-family: v.$font_variant;
                        font-size: 1.6rem;
                        line-height: 1.3;
                        font-weight: 100;
                        text-align: start;
                        margin: 1rem .5rem 4rem;
                        width: 100%;
                        @include m.tablet {
                            font-size: 3.6rem;
                            line-height: 1.2;
                        }
                    }
                }
                ul{
                    list-style: none;
                    margin: 0;
                    li{
                        color: v.$white;
                        font-family: v.$font_variant;
                        font-size: 1.6rem;
                        font-weight: 200;
                        margin: 2rem 0;
                        &:first-of-type{
                            margin-top: 0;
                        }
                        @include m.tablet {
                            font-size: 1.8rem;
                        }
                    }
                }
            }
            margin-bottom: 3rem;
            @include m.tablet {
                margin-bottom: 10rem;
            }

            .channels{
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                gap: 1rem 2rem;
                @include m.tablet {
                    gap: 1rem;
                }
                @include m.desktop {
                    grid-template-columns: repeat(3, 1fr);
                    gap: 1rem 4rem;
                }
                .channel{
                    transition: all .3s ease;
                    img{
                        display: block;
                        margin: 0 auto;
                        height: 4rem;
                        @include m.tablet {
                            height: 5rem;
                            
                        }
                        
                        @include m.desktop {
                            height: 8rem;
                            
                        }
                    }
                    p{
                        font-size: 1.6rem;
                        word-spacing: 0;
                        margin: 1rem 0;
                        text-align: center;
                        width: 100%;
                        padding: 0 1rem;

                        @include m.tablet {
                            font-size: 2rem;
                        }
                    }
                    &:hover{
                        transform: scale(1.1);
                    }
                }
            }
        }
        .full{
            margin-bottom: 10rem;
            .section_title{
                text-align: center;
                text-transform: uppercase;
                color: v.$white;
                font-size: 3rem;
                margin-bottom: 4rem;
                @include m.tablet {
                    font-size: 6rem;
                }
            }
            .niches{    
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                gap: 0 1rem;
                @include m.tablet {
                    grid-template-columns: repeat(3, 1fr);
                }
                @include m.desktop {
                    grid-template-columns: repeat(6, 1fr);
                }

                .niche{

                    p{
                        margin: 0;
                        height: 12rem;
                        align-content: start;
                        padding-top: .5rem;
                        word-spacing: 0;
                        text-align: center;
                        width: 100%;
                        word-break: break-word;

                        @include m.tablet {
                            font-size: 2rem;
                        }
                    }
                    picture{
                        display: block;
                        overflow: hidden;
                        img{
                            margin-bottom: 0;
                            transition: transform 0.5s ease;
                        }

                        &:hover img{
                            transform: scale(1.5);
                        }
                    }
                }
            }
            .full_width{
                width: 100%;
                margin-bottom: 6rem;
            }
            &.mbig{
                @include m.tablet {
                    margin-bottom: 30rem;
                }
            }
            ul{
                list-style: none;
                margin: 0;
                li{
                    color: v.$white;
                    font-family: v.$font_variant;
                    font-size: 1.6rem;
                    font-weight: 200;
                    margin: 2rem 0;
                    @include m.tablet {
                        font-size: 1.8rem;
                    }
                }
            }
        }
        p{
            font-size: 1.6rem;
            text-align: center;
            color: v.$white;
            line-height: 1.4;
            font-weight: 100;
            word-spacing: .1rem;
            letter-spacing: .1rem;
            font-family: v.$font_variant;
            @include m.tablet {
                margin: 2rem auto;
                font-size: 3rem;
                line-height: 1.2;
                word-spacing: .5rem;
                width: 70%;
            }
            
        }
        .button_div{
            margin-top: 5rem;
            @include m.tablet {
                display: flex;
                justify-content: flex-end;
            }
            .button_trnsp {
                @include m.button(transparent, v.$white, v.$white);
            }
        }
    }
}
.picture_cover{
    picture{
        img{
            margin-bottom: 30rem;
            width: 100%;

        }

    }
}

