@use 'base/variables' as v;
@use 'base/mixins' as m;

.home{
    .hero{
        background-image: linear-gradient(rgb(0, 0, 0), rgba(32, 32, 32, 0.7)),
        url(../../build/gif/gif3.gif);
        background-size: cover;
        background-position: center center;
        height: 100vh;
        max-width: 100%;
        align-content: end;
        
        .hero_content{
            h1 {
                text-align: start;
                padding: 0 2rem;
                font-size: 12rem;
                @include m.tablet {
                    font-size: 22rem; 
                }
                color: v.$white;
                font-weight: v.$bold;
                max-width: 100%;
                text-transform: uppercase;
                display: flex;
                gap: 1rem;
            }
        }
        .hero_content h1 {
            white-space: nowrap; // Evita que las palabras se dividan en mltiples lneas
            overflow: hidden; // Oculta el desbordamiento de las palabras
            span {
                gap: 1rem;
                display: inline-block;
                animation: moveLeft 60s linear infinite; // Aplica la animacin a cada palabra
            }
        }
        @keyframes moveLeft {
            0% { transform: translateX(4%); } // Inicia desde la derecha
            100% { transform: translateX(-100%); } // Se desplaza hacia la izquierda
            
            @include m.tablet {
                0% { transform: translateX(10%); } // Inicia desde la derecha
                100% { transform: translateX(-100%); } // Se desplaza hacia la izquierda
            }
        }
    }
    
    .intro{
        padding: 2rem;
        .intro_phrase{
            margin: 60rem 0;
            padding: 0;
            font-size: 5rem;
            @include m.tablet {
                font-size: 10rem;
            }
            @include m.desktop {
                font-size: 14rem;
            }
            text-transform: uppercase;
            color: v.$white;
        }
        .intro_div{
                padding: 0;
                margin: 60rem 0;
                @include m.tablet {
                    padding: 0 5rem;
                }
                &:last-of-type{
                    margin: 0;
                }
            .intro_title{
                text-align: center;
                text-transform: uppercase;
                color: v.$white;
                margin-bottom: 4rem;
                @include m.tablet {
                    margin-bottom: 10rem;
                }
                span{
                    padding: 0 1rem;
                    @include m.tablet {
                        padding: 0 2rem;
                        
                    }
                }
            }
            .intro_text{
                h3{
                    text-transform: uppercase;
                    margin-bottom: 1rem;
                    @include m.tablet {
                    }
                }
                p{
                    font-size: 2rem;
                    margin-bottom: 4rem;
                    @include m.tablet {
                        margin-bottom: 10rem;
                        font-size: 4rem;
                    }
                    color: v.$white;
                    line-height: 1.4;
                    font-weight: 100;
                    word-spacing: .1rem;
                    letter-spacing: .1rem;
                    font-family: v.$font_variant;
                    margin-top: 2rem;
    
                    @include m.tablet {
                        line-height: 1.2;
                        word-spacing: .5rem;
                    }
                }
                .p_end{
                    margin-bottom: 0;
                }
            }
            .button_div{
                margin-top: 5rem;
                @include m.tablet {
                    display: flex;
                    justify-content: flex-end;
                }
                .button_trnsp {
                    @include m.button(transparent, v.$white, v.$white);
                }
            }
            .details_services{
                summary{
                    text-align: center;
                    color: v.$white;
                    font-family: v.$font_variant;
                    font-size: 2rem;
                    border: solid .2rem v.$white;
                    padding: 1rem;
                    @include m.tablet {
                        font-size: 4rem;
                        margin-bottom: 3rem;
    
                    }
                    &:hover{
                        cursor: pointer;
                    }
                }
                .button_div{
                    margin-top: 1rem;
                    a{
                        width: 80%;
                        margin: auto;
                    }
                }
            }
        }
    }
}