@use 'base/variables' as v;
@use 'base/mixins' as m;
.cta{
    margin: 10rem 3rem 20rem;
    border-top: solid .1rem v.$white;
    @include m.tablet {
        margin: 14rem 15rem 20rem;
    }
    p{
        text-align: center;
        color: v.$white;
        font-family: v.$font_variant;
        font-size: 2.4rem;
        line-height: 1.1;
        word-spacing: .5rem;
        font-weight: 100;
        padding: 3rem 0;

        @include m.tablet {
            padding: 2rem 0;
            font-size: 4rem
        }
    }
    .button_trnsp{
        margin-top: 5rem;
        @include m.button(transparent, v.$white, v.$white);
        min-width: 100%;
    }
}