@use 'base/variables' as v;
@use 'base/mixins' as m;

.contact{
    .section_content{
        .title{
            font-size: 6rem;
            margin: 10rem 0;

            @include m.tablet {
                font-size: 12rem;
            }
        }
        .section_div{
            p{
                text-align: center;
                margin-top: 0;
                margin-bottom: 3rem;
        
                @include m.tablet {
                    width: 98%;
                    margin: 0 auto;
                    margin-bottom: 3rem;
                }
            }

            h2{
                text-align: center;
                margin: 0;
                padding: 4rem 0;
                text-transform: uppercase;
                color: v.$darkgold;

                @include m.tablet {
                    padding: 10rem 0;
                }
            }
            .contact_chnls{
                padding: 5rem 0;
                display: flex;
                gap: 6rem;
                justify-content: center;

                .contact_chnl{
                    display: flex;
                    flex-direction: column;
                    align-items: center;

                    a{
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                    }

                    img{
                        width: 12rem;

                        @include m.tablet {
                            width: 20rem;
                        }
                    }
                }
                p{
                    font-size: 1.6rem;
                    margin: 0;
                    padding: 2rem 0;

                    @include m.tablet {
                        font-size: 3rem;
                    }
                }
            }
        }
    }
}