@use 'base/variables' as v;
@use 'base/mixins' as m;

.team_table{
    margin: 0 auto;
    min-width: 100%;

    td, th{
        padding: 1rem .5rem;
        font-size: 1rem;
        max-width: 10rem;
        overflow-x: auto;
        text-align: center;
        color: v.$darkGrey;

        @include m.tablet {
            font-size: 1.6rem;
            padding: 1rem;
        }
    }

    .remove_btn{
        .mobile{
            display: block;
            font-size: 1.2rem;
            color: v.$white;
            margin: 0;
        }
        .p_tablet{
            color: v.$white;
            display: none;
        }
        @include m.tablet {
            p.mobile{
                display: none;
                
            }
            .p_tablet{
                display: block;
                font-size: 1.6rem;
                color: v.$white;
                margin: 0;
            }
        }
        @include m.button(transparent, v.$white, v.$white);
        padding: .2rem;
        
        
        @include m.tablet {
            margin: auto;
            padding: .6rem 0;

            max-width: 20rem;
        }
    }
}