@use 'base/variables' as v;
@use 'base/mixins' as m;

.us{
    .hero{
        background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(32, 32, 32, 0.3)),
        url(../../build/gif/logo_mov.gif);
        background-size: cover;
        max-width: 100%;
        align-content: end;
    }
    p{
        color: v.$lightGrey;
    }

    h2{
        color: v.$white;
        text-transform: uppercase;
        text-align: center;
        margin-top: 20rem;
        @include m.tablet {
            text-align: start;
            padding: 0 2rem 0;
            font-size: 5rem;
        }
    }
    .business{
        padding: 10rem 0;
        img{
            max-width: 100%;

            @include m.tablet {
                display: block;
                margin: 0 auto;
                max-width: 70%;
            }
        }

        .us_text{
            padding: 1rem;
            @include m.tablet {
                padding: 0;
            }
            p{
                font-family: v.$font_variant;
                line-height: 1.6;
                font-size: 1.6rem;

                @include m.tablet {
                    font-size: 3rem;
                    padding: 1rem 5rem;
                }
            }
        }
    }
}