@use 'base/variables' as v;
@use 'base/mixins' as m;

.panel{

    display: flex;

    .panel_dashboard{
        max-width: 90%;
        @include m.tablet {
            min-width: 50%;
        }
        padding: 2rem 1rem;
        margin: 0 auto;
        overflow: scroll;
        h2{
            text-align: center;
            color: v.$darkGrey;
        }
        hr{
            margin: 5rem 0;
        }

        .projects_list{
            list-style: none;
            padding: 0;
            margin: 0;

            .project{
                margin: 3rem 0;
                background-color: adjust-color(v.$lightGrey, $lightness: 5%);
                padding: 1rem;
                border-radius: 1rem;
                .project_div{
                    display: flex;
                    gap: .5rem;
                    justify-content: space-between;
                    margin: 0.5rem 0;
                    align-items: center;
                    align-content: center;
                    height: 4rem;
                    
                    .headers_div{
                        align-content: center;
                        height: 4rem;
                        background-color: v.$orangeFlame;
                        min-width: 10rem;
                        @include m.tablet {
                            min-width: 14rem;
                            
                        }

                        p{
                            color: v.$white;
                            text-align: center;
                            margin: 0;
                        }
                    }
                    
                    .content_div {
                        align-content: center;
                        height: 4rem;
                        flex: auto;
                        min-width: 6rem;
                        padding: 0 .2rem;
                        overflow-x: auto;
                        p{
                            text-wrap: nowrap;
                            color: v.$darkGrey;
                            margin: 0;

                        }
                    }
                }
                .project_actions{

                    height: 4rem;
                    border-top: solid .3rem v.$lightGrey;
                    display: flex;
                    padding-top: .4rem;
                    justify-content: center;
                    gap: 1rem;
                    
                    @include m.tablet {
                        gap: 5rem;
                        
                    }
                    
                    .button{
                        @include m.button(transparent, v.$white, v.$white);
                        
                    }
                    .delete{
                        @include m.button(transparent, v.$white, v.$white);
                    }

                    .button, .delete {
                        width: 14rem;
                        font-size: 1.2rem;
                        padding: 0;
                        align-content: center;
                        height: 3.6rem;
                        justify-content: center;
                        @include m.tablet {
                            width: 20rem;
                            font-size: 1.6rem;
                        }
                    }
                }
            }
        }
        .team{
            background-color: adjust-color(v.$lightGrey, $lightness: 5%);
            border-radius: 1.5rem;

            .invite_text{
                margin-top: 1rem;
                padding: 1rem;
                text-align: center;
                @include m.tablet {
                    padding: 2rem;
                }
                h3{
                    border-top: .3rem solid v.$lightGrey;
                    padding-top: 3rem;
                    color: v.$grey;
                }
            }
            .form{
                margin: 0 1rem;

                .button {
                    margin: 4rem auto;
                }
            }


        }
        .linkP{
            display: block;
            color: adjust-color(v.$darkGrey, $lightness: 20%);
            font-size: 1.4rem;
            font-weight: v.$bold;
            margin-bottom: 3rem;
            padding: 1rem;

            @include m.tablet {
                font-size: 1.8rem;
            }

            &:hover{
                color: adjust-color(v.$darkGrey, $lightness: 5%);
            }

            &.right{
                display: flex;
                justify-content: flex-end;
                margin-top: 3rem;
                margin-bottom: 0;
            }
        }
        .save_project{
            @include m.button(transparent, v.$white, v.$white);
            margin-top: 6rem;
            @include m.tablet {
                margin: 6rem auto;
            }
        }

        .existing-photos {
            display: grid;
            width: 31rem;
            margin: auto;
            @include m.tablet {
                width: 62rem;
            }
            img{
                margin: 0 auto;
                width: 10rem;
                height: 10rem
            }
            @include m.grid(3, .2rem);
            @include m.tablet {
                @include m.grid(6, .2rem);
            }
        }
        .photos{
            .photo_rmv_btn{
                display: block;
                background-color: adjust-color(v.$orangeFlame, $lightness: 5%);
                border: none;
                color: adjust-color(v.$white, $lightness: 15%);
                font-weight: v.$bold;
                border-radius: .3rem;
                padding: .4rem .7rem;
                position: relative;
                bottom: 10rem;
                left: 7.4rem;
                box-shadow: 0 0 .5rem v.$lightGrey;
                font-size: 1.2rem;
                transition: transform .3s ease;

                &:hover{
                    cursor: pointer;
                    background-color: adjust-color(v.$orangeFlame, $lightness: 5%);
                    transform: scale(1.15);
                }
            }
        }
    }
}