@use 'base/variables' as v;

.alert {
    display: block;
    width: 100%;
    padding: 1rem 1rem 1rem 2rem;
    margin: 1rem 0;
    font-size: 1.4rem;
    font-weight: v.$regular;
    text-align: center;

    &:last-of-type {
        margin-bottom: 3rem;
    }

    &.error {
        background-color: adjust-color( v.$orangeFlame, $lightness: 15% );
        color: adjust-color( v.$lightGrey, $lightness: 0% );
        font-size: 1.7rem;
        font-weight: v.$bold;
        border-left: solid .6rem adjust-color( v.$orangeFlame, $lightness: 5% );
    }

    &.success {
        background-color: adjust-color(v.$greenBrand, $lightness: 0%);
        color: v.$white;
        font-size: 1.7rem;
        font-weight: v.$bold;
        border-left: solid .6rem adjust-color( v.$greenBrand, $lightness: 10% );
    }
}