@use 'base/variables' as v;
@use 'base/mixins' as m;

.access {
    padding: 3rem;
    background-color: lighten(v.$lightGrey, 6%);
    height: 100vh;

    h3{
        text-align: center;
        margin-bottom: 6rem;
        color: v.$darkGrey;
        text-transform: uppercase;
    }
    label{
        color: v.$darkGrey
    }
    .login-btn {
        @include m.button(transparent, v.$white, v.$white);
        box-shadow: 0rem .6rem 1.5rem -.6rem v.$darkGrey;

        margin: 6rem auto;
    }
    .descripcion-pagina {
        margin-top: 10rem;
        text-align: center;
        font-size: 2.4rem;
    }

    .enlace-login{
        display: flex;
        color: v.$greenBrand;
        justify-content: center;
        font-size: 1.7rem;
    }
}