@use 'base/variables' as v;
@use 'base/mixins' as m;

.sidebar_background{
    background-color: v.$darkGrey;
    min-width: 6rem;
    transition: all .3s ease;
    min-height: 100vh;

    .sidebar{
        background-color: v.$darkGrey;
        height: 100%;
        width: 6rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 3rem 0.2rem;
        position: fixed;
        transition: all .3s ease;
        .upper_div{
            display: flex;
            flex-direction: column;
            gap: 8rem;
        }
        .up_bar{
            height: 7rem;
            align-content: center;
            .logo{
                display: none;
            }
            .bar_action_icon{
                width: 6rem;
            }
            .expand_icon{
                display: block;
                margin: 0 auto;
            }
            .collapse_icon{
                display: none;
            }
        }
        .under_div{

            align-content: center;

            p{display: none;}
            .button{
                display: none;
            }
            .logout_icon{
                display: block;
                width: 6rem;
                margin: 0 auto;
            }
        }
    
        .nav_bar{
            display: flex;
            flex-direction: column;
            padding: .3rem;
            .bar_link {
                display: flex;
                align-items: center;
                gap: 1rem;
                margin: 1.2rem 0;
                height: 5rem;
                .link_icon{
                    width: 5rem;
                }
                .link_text{
                    visibility: hidden;
                    transition: visibility .1s linear;
                }
            }
        }
    }
    &.expand{
        background-color: v.$darkGrey;
        @include m.tablet {
            min-width: 35rem;            
        }
        .sidebar{
            width: 90%;
            @include m.tablet {
                width: 35rem;                
            }
            padding: 3rem 1rem;

            .upper_div{
                justify-content: space-between;
            }
            .up_bar{
                display: flex;
                justify-content: space-between;
                align-items: center;
                .logo{
                    h1{
                        margin: 0;
                        font-size: 5rem;
                    }
                    color: v.$white;
                    display: block;
                }
                .expand_icon{
                    display: none;
                }
                .collapse_icon{
                    display: block;
                }
            }
        
            .nav_bar{
                align-items: flex-start;
                .bar_link {
                    .link_text{
                        visibility: visible;
                        margin: 0;
                        color: v.$white;
                        font-size: 2.4rem;
                    }
                }
            }
            p{
                display: block;
                text-align: center;
                color: v.$white;
                font-size: 2rem;
                
                span{
                    color: v.$yellow;
                    font-weight: v.$bold;
                }
            }
            .button{
                @include m.button(transparent, v.$white, v.$white);
                max-width: 100%;
                @include m.tablet {
                    margin: 0 auto;
                }

            }
            .logout_icon{
                display: none;
            }
        }
    }
}